import React from "react";
import { PowerOff, PowerOn, Pencil, Trash } from "react-feather";


type School = {
  id: string;
  name: string;
  email: string;
  createdAt: string;
  status?: string;
};

interface SchoolTableProps {
  schools: School[];
}

const SchoolTable: React.FC<SchoolTableProps> = ({ schools }) => (
  <div className="overflow-x-auto rounded-xl border border-gray-200 shadow-sm">
    <table className="min-w-full text-sm text-left">
      <thead className="bg-gray-50 text-gray-600 uppercase text-xs">
        <tr>
          <th className="px-6 py-4">ឈ្មោះ</th>
          <th className="px-6 py-4">អ៊ីមែល</th>
          <th className="px-6 py-4">ថ្ងៃចុះឈ្មោះ</th>
          <th className="px-6 py-4">ស្ថានភាព</th>
          <th className="px-6 py-4">សកម្មភាព</th>
        </tr>
      </thead>
      <tbody className="divide-y divide-gray-100 bg-white">
        {(schools ?? []).map((school) => (
          <tr key={school.id}>
            <td className="px-6 py-4 flex items-center gap-3">
              {/* Avatar */}
              <div className="w-8 h-8 rounded-full bg-blue-100 text-blue-700 flex items-center justify-center font-bold">
                {school.name?.charAt(0).toUpperCase()}
              </div>
              <span>{school.name}</span>
            </td>
            <td className="px-6 py-4">{school.email}</td>
            <td className="px-6 py-4">
              {new Date(school.createdAt).toLocaleDateString("en-US", {
                month: "short",
                day: "numeric",
                year: "numeric",
              })}
            </td>
            <td className="px-6 py-4">
              {school.status !== "deactivated" ? (
                <span className="bg-green-100 text-green-700 px-2 py-1 rounded">សកម្ម</span>
              ) : (
                <span className="bg-red-100 text-red-700 px-2 py-1 rounded">អសកម្ម</span>
              )}
            </td>
            <td className="px-6 py-4">
              {school.status !== "deactivated" ? (
                <Button className="bg-yellow-100 text-yellow-700 px-2 py-1 rounded mr-2 flex items-center gap-1">
                  <PowerOff size={16} /> Deactivate
                </Button>
              ) : (
                <Button className="bg-blue-100 text-blue-700 px-2 py-1 rounded mr-2 flex items-center gap-1">
                  <PowerOn size={16} /> Activate
                </Button>
              )}
              <Button className="bg-gray-100 text-gray-700 px-2 py-1 rounded mr-2 flex items-center gap-1">
                <Pencil size={16} /> Edit
              </Button>
              <Button className="bg-red-100 text-red-700 px-2 py-1 rounded flex items-center gap-1">
                <Trash size={16} /> លុប
              </Button>
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>
);

export default React.memo(SchoolTable);